<template >
<div>
<van-nav-bar
  left-arrow
  @click-left="onClickLeft"
/>
    <div class="content"> 
    <div class="photo"> <img :src="movie.medium" alt=""> </div>
    <div class="title"><p>{{movie.title}}</p> <span class="longtime">{{movie.longtime}} </span> <p class="ename">{{movie.original_title}} </p></div> 
    <div class="score"><p> ★★★★☆ {{movie.rating_average}} <span>/10</span><span class="icon"><img src="../../assets/images/icons/comment.png" alt=""> <img src="../../assets/images/icons/star.png" alt=""></span> </p> </div>
    <div class="nav">
        <ul>
        <li class="current">简介</li>
        <li @click="toComment()">影评</li>
        <li >讨论</li>
        <li>更多</li>
        </ul>
    </div>
<div class="introduction">
    <p>{{movie.summary}} </p><label><span>...</span> 查看更多></label> 
    </div>  
<h3>演职员表</h3>
 <div class="list-actor">
      <swiper ref="mySwiper" :options="swiperActor">
    <swiper-slide v-for="item in movie.players" :key="item.id" >
        <img :src="item.avatar" alt="" @click="todetails(item.playerId)"><p>{{item.name.replace(/[a-z]|[A-Z]/g,'')}}</p><p class="role">{{item.role}} </p>
    </swiper-slide>
  </swiper>
  <p class="allactor">全部{{length}}位演员</p>
     </div>    
<h3>视频</h3> 
<div class="video-list">
      <swiper ref="mySwiper" :options="swiperVideo">
    <swiper-slide v-for="(item,index) in movie.trailers" :key="item" >
       <video :src="item" @click="topause(index)"></video><div class="play" @click="play(index)">03：34</div>
    </swiper-slide>
  </swiper>
</div>
<h3>票房</h3> 
<div class="boxoffice">
    <div class="num"> <p>2</p> <span>今日票房排行</span></div>
    <div class="num"> <p>暂无</p> <span>首周票房(万)</span></div>
    <div class="num"> <p>2660</p> <span>今日票房排行</span></div>
</div>    
<p class="bfdetails">票房详情</p>
    </div>
<van-button type="primary" size="large" @click="tocTicket">特惠选座</van-button>
    </div>
</template>

<script>
import Vue from 'vue';
import { Rate } from 'vant';

Vue.use(Rate);
import axios from 'axios'
export default {
    data() {
      return {
          movie:[],
          length:1,
          swiperActor:{
            slidesPerView:3.1,
            spaceBetween: 5,
            centeredSlides: false,
            loop:false,
        },
        swiperVideo:{
            slidesPerView:1.5,
            spaceBetween: 5,
            centeredSlides: false,
            loop:false,
        }
      }

    },
    created(){
        this.$store.state.currentPage = this.$router.currentRoute.fullPath

        console.log(this.$route.params.id)
				axios.get(`http://bufantec.com/api/douban/movie/subject`,{params:{mId:this.$route.params.id}}).then(res=>{
						//console.log(res.data.data.list)
						//console.log(this)
						this.movie=res.data.data
                        console.log(this.movie);
                        this.length=this.movie.players.length;
                        console.log(this.movie.trailers)
             })	               
    },
    methods:{
        tocTicket() {
            this.$router.push('/cinema/cticket')
        },

  onClickLeft() {
            this.$router.go(-1);
  },
  todetails(id){
            console.log(id)
            this.$router.push({name:'actor', params:{id:id}})

  },
  toComment(){
      var a=this.$route.params.id;
      console.log(a)
      this.$router.push({name:'comment', params:{id:a}})
  },
            play(index){
               var video=document.getElementsByTagName('video')[index];
                var play=document.getElementsByClassName('play')[index];
                // play.style.display='none';
                console.log(index)
                if(video.paused){
                    video.play();
                    play.style.display='none';
                }else{
                    video.pause();
                }
            var au=document.getElementsByTagName("video");
           for(var i=0;i<au.length;i++){
            au[i].addEventListener("playing", function(){
                var this1=this;
                console.log(this1)
                for(var b=0;b<au.length;b++){
                    var this2=au[b];
                    if(this1!==this2){
                        this2.pause();
                    }
                }
            });
        }
        },
            topause(index){
                var play=document.getElementsByClassName('play')[index];
                if(event.target.played){
                    event.target.pause()
                    play.style.display='block';
                }else if(event.target.paused){
                    event.target.played()
                }
                }
    }
}
</script>



